import React, { Component } from "react";
import { Form, InputNumber, DatePicker } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";

import moment from "moment";

const FormItem = Form.Item;
// const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false,
    date: moment()
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;
  
    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)
    ) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  handleClickOutsideDate = e => {
    const { editing } = this.state;
    const elem_class = e.target.getAttribute("class")
    const elem_role = e.target.getAttribute("role")

    if (editing && !this.cell) this.toggleEdit();

    // ketika klik input date
    else if(editing && elem_class === "ant-calendar-picker-input ant-input"){
      return;
    }

    // ketika klik now
    else if(editing && this.cell && elem_role === "button" && elem_class === "ant-calendar-today-btn ") {
      return;
    }

    // ketika klik ok
    else if(editing && this.cell && elem_role === "button" && elem_class === "ant-calendar-ok-btn") {
      this.saveDate();
      this.toggleEdit();
      document.removeEventListener("click", this.handleClickOutsideDate, true);
    }

    // ketika klik tahun/bulan
    else if(editing && this.cell && elem_role === "button") {
      return;
    }

    // ketika klik tahun di panel
    else if(editing && this.cell && elem_class === "ant-calendar-year-panel-year") {
      return;
    }

    // ketika klik bulan di panel
    else if(editing && this.cell && elem_class === "ant-calendar-month-panel-month") {
      return;
    }

    // ketika klik tanggal
    else if(editing && this.cell && elem_class === "ant-calendar-date") {
      return;
    }

    // ketika klik diluar
    else if(editing && this.cell && !this.cell.contains(e.target)) {
      this.saveDate();
      this.toggleEdit();
      document.removeEventListener("click", this.handleClickOutsideDate, true);
    }
  }

  save = (name, id) => {
    const { record, handleSave } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      if (name && name === "opening_balance_date") {
        const newRecord = Object.assign({}, record, {
          opening_balance_date: id.format("YYYY-MM-DD HH:mm:ss")
        });

        this.setState({ date: id })
        handleSave(newRecord);
      } else {
        this.toggleEdit();
        handleSave({ ...record, ...values });
      }
    });
  };

  saveDate = () => {
    const { record, handleSave } = this.props;
    const { date } = this.state
    const newRecord = Object.assign({}, record, {
      opening_balance_date: date.format("YYYY-MM-DD HH:mm:ss")
    });
    
    handleSave(newRecord);
  }

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable && record ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "opening_balance") {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "opening_balance_date") {
                document.addEventListener(
                  "click",
                  this.handleClickOutsideDate,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: moment(
                        record[dataIndex],
                        "YYYY-MM-DD HH:mm:ss"
                      )
                    })(
                      <DatePicker
                        ref={node => (this.input = node)}
                        allowClear={false}
                        style={{ width: "100%" }}
                        showTime
                        onChange={this.save.bind(this, "opening_balance_date")}
                        format="DD MMMM YYYY HH:mm:ss"
                      />
                    )}
                  </FormItem>
                );
              } else if (!editing && dataIndex === "opening_balance") {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? parseFloat(record[dataIndex])
                          .toFixed(2)
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      : "0.00"}
                  </div>
                );
              } else if (!editing && dataIndex === "opening_balance_date") {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? moment(record[dataIndex], "YYYY-MM-DD HH:mm:ss").format(
                          "DD MMMM YYYY HH:mm:ss"
                        )
                      : moment().format("DD MMMM YYYY HH:mm:ss")}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
