import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import {
  Alert,
  Button,
  Col,
  Empty,
  Form,
  Modal,
  Row,
  Select,
  Spin
} from "antd";
import EditableTable from "./editableTable";

const { Option } = Select;

// const { Option } = Select;
class ModalOpeningBalance extends Component {
  state = {
    opening_balance_details: [],
    opening_balances: [],
    id_location: null,
    selected_locations: [],
    limit: 0,
    clear: false,
    changed: false
  };

  componentDidUpdate = () => {
    const { opening_balances } = this.props;
    const { limit } = this.state;
    if (
      Array.isArray(opening_balances) &&
      opening_balances.length > 0 &&
      !limit
    ) {
      this.setState({
        opening_balance_details: opening_balances,
        opening_balances: opening_balances,
        limit: 1
      });
    }
  };

  handleSelectLocation = value => {
    this.setState({ id_location: value });
  };

  handleSaveOpeningBalanceList = (opening_balances, selected_locations) => {
    const prev_clear = this.state.clear;

    this.setState({
      opening_balance_details: opening_balances,
      selected_locations: selected_locations,
      clear: false,
      changed: prev_clear ? false : true
    });

    if (prev_clear) {
      this.props.submit([]);
      this.props.handleModal();
    }
  };

  clearLocation = (opening_balances, selected_locations) => {
    this.setState({
      opening_balance_details: opening_balances,
      id_location: null,
      selected_locations: selected_locations,
      changed: true
    });
  };

  clearOpeningBalances = (opening_balances, selected_locations) => {
    this.setState({
      opening_balance_details: opening_balances,
      opening_balances: [],
      selected_locations: selected_locations
    });
  };

  handleSubmit = () => {
    const { opening_balance_details } = this.state;
    this.setState({ changed: false });
    this.props.submit(opening_balance_details);
  };

  render() {
    const { id_location, selected_locations, opening_balances } = this.state;
    const {
      id_currency,
      locations,
      currencies,
      rate,
      loadingForeign
    } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="button.openingBalanceLocation" />}
        visible={this.props.visible}
        centered
        closable={false}
        maskClosable={false}
        width="90%"
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>

              <Button
                onClick={() => {
                  this.setState({ clear: true });
                }}
                disabled={this.props.used ? true : false}
              >
                <IntlMessages id="button.deleteAll" />
              </Button>

              <Button
                type="primary"
                onClick={this.handleSubmit}
                disabled={this.props.used ? true : false}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
      >
        {this.state.changed ? (
          <Alert
            message={<IntlMessages id="salesInvoices.saveMultiPayment" />}
            type="warning"
            showIcon
            style={{ marginBottom: "10px" }}
          />
        ) : null}

        <Row>
          <Col span={24}>
            <Form.Item {...formItemLayout}>
              <Select
                allowClear
                filterOption={false}
                placeholder={<IntlMessages id="select.location" />}
                loading={loadingForeign}
                notFoundContent={
                  loadingForeign ? (
                    <Spin size="small" />
                  ) : (
                    <Empty description={<IntlMessages id="noData" />} />
                  )
                }
                onSelect={this.handleSelectLocation}
                optionFilterProp="children"
                style={{ width: "100%" }}
                filterOption={true}
                value={undefined}
                disabled={this.props.used}
              >
                {locations && locations.length > 0
                  ? locations.map(data =>
                      !selected_locations.includes(data.id_location) ? (
                        <Option key={data.id_location} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ) : null
                    )
                  : null}
              </Select>
            </Form.Item>
          </Col>
        </Row>

        <Row style={{ height: "300px" }}>
          <Col span={24}>
            <EditableTable
              id_location={id_location}
              locations={locations}
              opening_balances={opening_balances}
              handleSaveOpeningBalanceList={this.handleSaveOpeningBalanceList}
              clearLocation={this.clearLocation}
              clearOpeningBalances={this.clearOpeningBalances}
              id_currency={id_currency}
              rate={rate}
              currencies={currencies}
              used={this.props.used}
              clear={this.state.clear}
            />
          </Col>
        </Row>
      </Modal>
    );
  }
}

const WrappedModalOpeningBalance = Form.create({
  name: "opening_balance_modal"
})(ModalOpeningBalance);

export default WrappedModalOpeningBalance;
