import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Input, Select, Tree, Row, Col, Divider, Empty, Spin } from "antd";

// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import accountActions from "../../../../redux/account/account/actions";
// import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box } from "../../../App/app.style";
import AccountForm from "./form";

const Option = Select.Option;
const Search = Input.Search;
const { TreeNode } = Tree;

// Searching Item
const getParentKey = (key, tree) => {
  let parentKey;
  for (let i = 0; i < tree.length; i++) {
    const node = tree[i];
    if (node.children_all) {
      if (node.children_all.some(account => account.id_account === key)) {
        parentKey = node.id_account;
      } else if (getParentKey(key, node.children_all)) {
        parentKey = getParentKey(key, node.children_all);
      }
    }
  }
  return parentKey;
};

const redirectTo = "/dashboard/setup/menus";

class Account extends Component {
  state = {
    expandedKeys: [],
    searchValue: "",
    autoExpandParent: true,
    accountType: null,
    id_account: null,
    newAccount: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getAccounts } = this.props;
    getAccounts();
  }

  // Display notif
  componentDidUpdate() {
    const { getAccounts } = this.props;

    // Display error notif
    if (
      !this.props.account.loadingSubmit &&
      !this.props.account.loadingAccount &&
      !this.props.account.loadingAccounts &&
      !this.props.account.loadingAccountList &&
      !this.props.account.loadingForeign &&
      this.props.account.error
    ) {
      notification("error", this.props.account.error);
      this.props.clearError();
    }

    // Display success notif
    if (
      !this.props.account.loadingSubmit &&
      !this.props.account.loadingAccount &&
      !this.props.account.loadingAccounts &&
      !this.props.account.loadingAccountList &&
      !this.props.account.loadingForeign &&
      this.props.account.success
    ) {
      notification("success", this.props.account.success);
      this.props.clearSuccess();

      // Clear state new Account
      this.setState({
        ...this.state,
        newAccount: null,
        id_account: this.props.account.account
          ? this.props.account.account.id_account
          : null
      });

      getAccounts(this.state.accountType);
    }
  }

  // Handle change account type
  handleChangeAccountType = e => {
    const { getAccounts } = this.props;
    this.setState({
      ...this.state,
      accountType: e,
      newAccount: null
    });

    getAccounts(e);
  };

  // Handle change search
  handleSearchChange = e => {
    const { accountList, accounts } = this.props.account;
    const value = e.target.value;
    const expandedKeys = accountList.map(account => {
      const name = account.account_code + " " + account.account_name;
      if (name.toLowerCase().indexOf(value.toLowerCase()) > -1) {
        return getParentKey(account.id_account, accounts);
      }
      return null;
    });

    this.setState({
      expandedKeys,
      searchValue: value,
      autoExpandParent: true
    });
  };

  // Handle Add Button
  handleAddButton = () => {
    const { accountType } = this.state;
    const { clearAccount } = this.props;
    this.setState({
      ...this.state,
      newAccount: {
        id_account_parent: null,
        id_account_type: accountType ? accountType : null,
        account_code: null
      }
    });
    clearAccount();
  };

  // Handle select tree
  handleSelectTree = e => {
    const { showAccount, clearAccount } = this.props;
    this.props.clearErrorField();

    this.setState({
      ...this.state,
      newAccount: null
    });

    const value = e[0] ? e[0] : "";
    // console.log(value);
    if (value.indexOf("add") > -1) {
      const [
        add,
        id_account_parent,
        id_account_type,
        account_code
      ] = value.split("|");
      clearAccount();
      this.setState({
        ...this.state,
        newAccount: {
          id_account_parent: id_account_parent,
          id_account_type: id_account_type,
          account_code: account_code + "."
        }
      });
    } else {
      value ? showAccount(value) : clearAccount();
      this.setState({
        ...this.state,
        id_account: value ? value : null,
        newAccount: null
      });
    }
  };

  // Handle expand tree
  onExpand = expandedKeys => {
    this.setState({
      expandedKeys,
      autoExpandParent: false
    });
  };

  // Handle submit
  handleSubmit = value => {
    // console.log(newAccount);
    // console.log(this.state.id_account);
    const { id_account, newAccount } = this.state;
    const { storeAccount, updateAccount } = this.props;

    newAccount
      ? (value = Object.assign({}, value, {
          id_account_parent: newAccount.id_account_parent
        }))
      : (value = Object.assign({}, value));
    // console.log(id_account);
    newAccount ? storeAccount(value) : updateAccount(id_account, value);
    this.props.clearErrorField();
  };

  // Handle Activate account
  handleActivate = id_account => {
    const { activateAccount } = this.props;
    // console.log(id_account);
    activateAccount(id_account);
  };

  // Handle dectivate account
  handleDeactivate = id_account => {
    const { deactivateAccount } = this.props;
    // console.log(id_account);
    deactivateAccount(id_account);
  };

  render() {
    // console.log(this.props);
    // console.log(this.state);
    const { accounts, account, accountTypes } = this.props.account;
    const {
      loadingSubmit,
      loadingAccount,
      loadingAccounts,
      loadingAccountList,
      loadingForeign
    } = this.props.account;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const checkLoading = () => {
      if (loadingAccounts || loadingAccountList || loadingForeign) {
        return true;
      }
      return false;
    };

    const {
      searchValue,
      expandedKeys,
      autoExpandParent,
      newAccount
    } = this.state;

    const loop = data =>
      data.map(account => {
        const name = account.account_code + " " + account.account_name;
        const key = account.account_code + " " + account.account_name;
        const index = key.toLowerCase().indexOf(searchValue.toLowerCase());
        const beforeStr = name.substr(0, index);
        const str = name.substr(index, searchValue.length);
        const afterStr = name.substr(index + searchValue.length);
        const title =
          index > -1 ? (
            <span>
              {beforeStr}
              <strong>{str}</strong>
              {afterStr}
            </span>
          ) : (
            <span>{name}</span>
          );
        if (account.children_all) {
          return (
            <TreeNode
              key={account.id_account}
              title={
                account.is_active ? (
                  title
                ) : (
                  <span style={{ color: "#f5222d" }}>{title}</span>
                )
              }
            >
              {permissions.includes("Create Account") &&
              !account.is_used &&
              account.is_active ? (
                <TreeNode
                  key={
                    "add|" +
                    account.id_account +
                    "|" +
                    account.id_account_type +
                    "|" +
                    account.account_code
                  }
                  title={<IntlMessages id="accounts.add" />}
                />
              ) : null}
              {loop(account.children_all)}
            </TreeNode>
          );
        }
        return <TreeNode key={account.id_account} title={title} />;
      });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="accounts.pageHeader" />}
            style={{ margin: 0 }}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Account") ? (
                <Col span={13}>
                  <Row type="flex" justify="end">
                    <Col>
                      <Button type="primary" onClick={this.handleAddButton}>
                        <IntlMessages id="accounts.add" />
                      </Button>
                    </Col>
                  </Row>
                </Col>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            {/* <InputGroup compact> */}
            <Col span={11}>
              <Col span={11}>
                <Select
                  value={checkLoading() ? null : this.state.accountType}
                  onChange={this.handleChangeAccountType}
                  loading={checkLoading()}
                  style={{ width: "100%" }}
                  showSearch
                >
                  <Option value={null}>
                    <IntlMessages id="accounts.accountType.options.all" />
                  </Option>
                  {accountTypes.map((data, key) => (
                    <Option key={key} value={data.id_account_type}>
                      {data.account_type_name}
                    </Option>
                  ))}
                </Select>
              </Col>
              <Col span={12}>
                <Search
                  placeholder="Account"
                  disabled={checkLoading()}
                  allowClear
                  onChange={this.handleSearchChange}
                />
              </Col>
            </Col>

            {/* </InputGroup> */}
          </div>

          <CardWrapper>
            <Col span={11}>
              <Col span={23}>
                {checkLoading() ? (
                  <div className="spinStyle">
                    <Spin />
                  </div>
                ) : accounts.length === 0 ? (
                  <Empty description={<IntlMessages id="noData" />} />
                ) : (
                  <Tree
                    showIcon
                    showLine
                    onExpand={this.onExpand}
                    expandedKeys={expandedKeys}
                    autoExpandParent={autoExpandParent}
                    onSelect={this.handleSelectTree}
                  >
                    {loop(accounts)}
                  </Tree>
                )}
              </Col>
              <Col span={1}>
                <Divider type="vertical" style={{ height: "100vh" }} />
              </Col>
            </Col>
            <Col span={13}>
              {loadingAccount && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin />
                </div>
              ) : account ? (
                <AccountForm
                  submit={this.handleSubmit}
                  account={this.props.account}
                  auth={this.props.auth}
                  activate={this.handleActivate}
                  deactivate={this.handleDeactivate}
                />
              ) : newAccount ? (
                <AccountForm
                  submit={this.handleSubmit}
                  account={this.props.account}
                  auth={this.props.auth}
                  newAccount={this.state.newAccount}
                />
              ) : (
                <Empty
                  description={<IntlMessages id="accounts.noSelection" />}
                />
              )}
            </Col>
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { account: state.account, auth: state.auth };
};

export default compose(
  connect(
    mapStateToProps,
    accountActions
  )
)(Account);
