const actions = {
  getAccounts: id_account_type => ({
    type: "GET_ACCOUNTS_REQUEST",
    payload: { id_account_type: id_account_type }
  }),
  storeAccount: body => ({
    type: "STORE_ACCOUNT_REQUEST",
    payload: { body: body }
  }),
  showAccount: id_account => ({
    type: "SHOW_ACCOUNT_REQUEST",
    payload: { id_account: id_account }
  }),
  updateAccount: (id_account, body) => ({
    type: "UPDATE_ACCOUNT_REQUEST",
    payload: { id_account: id_account, body: body }
  }),
  activateAccount: id_account => ({
    type: "ACTIVATE_ACCOUNT_REQUEST",
    payload: { id_account: id_account }
  }),
  deactivateAccount: id_account => ({
    type: "DEACTIVATE_ACCOUNT_REQUEST",
    payload: { id_account: id_account }
  }),
  clearAccount: () => ({
    type: "CLEAR_ACCOUNT"
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
