import React, { Component } from "react";
// import moment from "moment";

import { Button, Form, Popconfirm } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import moment from "moment";
import { getCurrencyRate } from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange"

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);

    this.state = {
      dataSource: [],
      selected_locations: [],
      count: 1
    };
  }

  componentDidMount = () => {
    const {
      id_location,
      locations,
      opening_balances,
      id_currency,
      currencies,
      rate,
      clearLocation,
      clearOpeningBalances
    } = this.props;

    if (id_location) {
      const { dataSource, selected_locations, count } = this.state;
      const location = locations.find(
        location => location.id_location === id_location
      );

      const init = {
        id_location: id_location,
        location_code: location.location_code,
        location_name: location.location_name,
        opening_balance_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        ob_rate: parseFloat(rate) ? parseFloat(rate) : 1,
        opening_balance: 0
      };

      const newData = [...dataSource, init];
      const newSelectedLocations = [...selected_locations, init.id_location];

      this.setState({
        dataSource: newData,
        selected_locations: newSelectedLocations,
        count: count + 1
      });
      clearLocation(newData, newSelectedLocations);
    } else if (Array.isArray(opening_balances) && opening_balances.length > 0) {
      let newData = [];
      let selected_locations = [];
      let newIndex = 0;

      opening_balances.map(opening_balance => {
        const location = locations.find(
          location => location.id_location === opening_balance.id_location
        );
        const currency = currencies.find(
          data => data.id_currency === id_currency
        );

        const new_rate = getCurrencyRate(
          currency.currency_rate,
          opening_balance.opening_balance_date
        );

        newData.push({
          id_location: location.id_location,
          location_code: location.location_code,
          location_name: location.location_name,
          opening_balance_date: opening_balance.opening_balance_date,
          ob_rate: new_rate ? new_rate : 1,
          opening_balance: opening_balance.opening_balance
            ? parseFloat(opening_balance.opening_balance)
            : 0
        });
        selected_locations.push(location.id_location);

        newIndex = parseInt(opening_balance.index_no) + 1;
      });

      this.setState({
        dataSource: [...newData],
        selected_locations: selected_locations,
        count: newIndex
      });
      clearOpeningBalances(newData, selected_locations);
    }
  };

  componentDidUpdate = prevProps => {
    const {
      id_location,
      locations,
      opening_balances,
      id_currency,
      currencies,
      rate,
      handleSaveOpeningBalanceList,
      clearLocation,
      clearOpeningBalances,
      clear
    } = this.props;

    if (id_location) {
      const { dataSource, selected_locations, count } = this.state;
      const location = locations.find(
        location => location.id_location === id_location
      );

      const init = {
        id_location: id_location,
        location_code: location.location_code,
        location_name: location.location_name,
        opening_balance_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        ob_rate: parseFloat(rate) ? parseFloat(rate) : 1,
        opening_balance: 0
      };

      const newData = [...dataSource, init];
      const newSelectedLocations = [...selected_locations, init.id_location];

      this.setState({
        dataSource: newData,
        selected_locations: newSelectedLocations,
        count: count + 1
      });
      clearLocation(newData, newSelectedLocations);
    } else if (Array.isArray(opening_balances) && opening_balances.length > 0) {
      let newData = [];
      let selected_locations = [];
      let newIndex = 0;

      opening_balances.map(opening_balance => {
        const location = locations.find(
          location => location.id_location === opening_balance.id_location
        );
        newData.push({
          id_location: location.id_location,
          location_code: location.location_code,
          location_name: location.location_name,
          opening_balance_date: opening_balance.opening_balance_date,
          ob_rate: opening_balance.rate ? parseFloat(opening_balance.rate) : 1,
          opening_balance: opening_balance.opening_balance
            ? parseFloat(opening_balance.opening_balance)
            : 0
        });
        selected_locations.push(location.id_location);

        newIndex = parseInt(opening_balance.index_no) + 1;
      });

      this.setState({
        dataSource: [...newData],
        selected_locations: selected_locations,
        count: newIndex
      });
      clearOpeningBalances(newData, selected_locations);
    } else if (prevProps.id_currency !== id_currency) {
      let newData = [];
      let selected_locations = [];
      let newIndex = 0;

      this.state.dataSource.map(opening_balance => {
        const currency = currencies.find(
          data => data.id_currency === id_currency
        );

        const new_rate = getCurrencyRate(
          currency.currency_rate,
          opening_balance.opening_balance_date
        );

        newData.push({
          id_location: opening_balance.id_location,
          location_code: opening_balance.location_code,
          location_name: opening_balance.location_name,
          opening_balance_date: opening_balance.opening_balance_date,
          ob_rate: new_rate ? parseFloat(new_rate) : 1,
          opening_balance: opening_balance.opening_balance
            ? parseFloat(opening_balance.opening_balance)
            : 0
        });

        selected_locations.push(opening_balance.id_location);
        newIndex = parseInt(opening_balance.index_no) + 1;
      });

      this.setState({
        dataSource: [...newData],
        selected_locations: selected_locations,
        count: newIndex
      });
      handleSaveOpeningBalanceList(newData, selected_locations);
    } else if (clear) {
      this.setState({
        dataSource: [],
        selected_locations: [],
        count: 1
      })

      handleSaveOpeningBalanceList([], []);
    }
  };

  handleDelete = id_location => {
    const { handleSaveOpeningBalanceList } = this.props;

    const dataSource = this.state.dataSource;
    const selected_locations = this.state.selected_locations;

    const newData = dataSource.filter(
      currData => currData.id_location !== id_location
    );
    const newSelectedLocations = selected_locations.filter(
      currData => currData !== id_location
    );

    this.setState({
      dataSource: [...newData],
      selected_locations: [...newSelectedLocations],
      count: newData.length + 1
    });

    handleSaveOpeningBalanceList(newData, newSelectedLocations);
  };

  handleSave = row => {
    const { selected_locations } = this.state;
    const {
      id_currency,
      currencies,
      handleSaveOpeningBalanceList
    } = this.props;
    const newData = this.state.dataSource;

    const index = newData.findIndex(
      currData => row.id_location === currData.id_location
    );

    if (
      !moment(newData[index].opening_balance_date).isSame(
        row.opening_balance_date
      )
    ) {
      const currency = currencies.find(
        data => data.id_currency === id_currency
      );
      const rate = getCurrencyRate(
        currency.currency_rate,
        row.opening_balance_date
      );
      row.ob_rate = rate;
    }

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    this.setState({
      dataSource: [...newData]
    });

    // console.log("new Datas", newData);
    handleSaveOpeningBalanceList(newData, selected_locations);
  };

  render() {
    const columnsRaw = [
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_code",
        rowKey: "location_code",
        width: "30%",
        render: (text, row) => `${text} (${row.location_name})`
      },
      {
        title: getLanguage({ id: "asOf" }),
        dataIndex: "opening_balance_date",
        rowKey: "opening_balance_date",
        width: "25%",
        editable: true,
        render: text =>
          moment(text, "YYYY-MM-DD HH:mm:ss").format("DD MMMM YYYY HH:mm:ss")
      },
      {
        title: getLanguage({ id: "rate" }),
        dataIndex: "ob_rate",
        rowKey: "ob_rate",
        width: "15%",
        render: text =>
          parseFloat(text)
            .toFixed(2)
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      },
      {
        title: getLanguage({ id: "accounts.openingBalance" }),
        dataIndex: "opening_balance",
        rowKey: "opening_balance",
        width: "20%",
        editable: true,
        render: text =>
          parseFloat(text)
            .toFixed(2)
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        render: (text, row) => {
          //checking transaction status, will be fixed soon
          return (
            <div>
              <Popconfirm
                title={<IntlMessages id="confirmDelete" />}
                onConfirm={() => this.handleDelete(row.id_location)}
              >
                <Button
                  style={{ borderRadius: "50%" }}
                  icon="delete"
                  disabled={this.props.used}
                />
              </Popconfirm>
            </div>
          );
        }
      }
    ];
    // console.log(this.state.dataSource);
    // const { totalAmount } = this.props;
    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = columnsRaw.map(col => {
      if (!col.editable) {
        return col;
      } else {
        return {
          ...col,
          onCell: record => ({
            record,
            editable: this.props.used ? false : col.editable,
            dataIndex: col.dataIndex,
            title: col.title,
            handleSave: this.handleSave
          })
        };
      }
    });

    return (
      <>
        <TableWrapper
          components={components}
          rowKey={record => record.id_location}
          rowClassName={() => "editable-row"}
          dataSource={dataSource}
          columns={columns}
          pagination={false}
          scroll={{ y: 280 }}
        />
      </>
    );
  }
}

export default EditableTable;
