import React, { Component } from "react";
import moment from "moment";

import Popconfirms from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, Row, Alert, Icon } from "antd";
import { getCurrencyRate } from "../../../../helpers/helper";

import ModalOpeningBalance from "./modalOpeningBalance";
import getLanguage from "../../../App/MyLanguange"
const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class AccountForm extends Component {
  state = {
    opening_balances: [],
    currency_is_base: false,
    currency_rate: 1,
    disabled: false,
    updated: false,
    visible: false
  };

  componentDidMount = () => {
    const { account, newAccount } = this.props;

    let data = newAccount
      ? Object.assign({}, newAccount)
      : account.account
        ? Object.assign({}, account.account)
        : [];

    // set currency rate global ketika show account
    if (data.id_currency) {
      this.setFieldRate(data.id_currency, moment(), data.opening_balances);
    }
  };

  componentDidUpdate = (prevProps, prevState) => {
    const { account, newAccount } = this.props;
    const { currencies } = account;

    let data = newAccount
      ? Object.assign({}, newAccount)
      : account.account
        ? Object.assign({}, account.account)
        : [];

    if (
      data.id_currency &&
      prevState.currency_rate !== this.state.currency_rate
    ) {
      let opening_balances = [];
      this.state.opening_balances.map(opening_balance => {
        const currency = currencies.find(
          data => data.id_currency === data.id_currency
        );

        const new_rate = getCurrencyRate(
          currency.currency_rate,
          opening_balance.opening_balance_date
        );

        opening_balances.push({
          id_location: opening_balance.id_location,
          location_code: opening_balance.location_code,
          location_name: opening_balance.location_name,
          opening_balance_date: opening_balance.opening_balance_date,
          ob_rate: new_rate ? new_rate : 1,
          opening_balance: opening_balance.opening_balance
            ? parseFloat(opening_balance.opening_balance)
            : 0
        });
      });
    }
  };

  setFieldRate = (id_currency, date, opening_balances) => {
    const { currencies } = this.props.account;

    let currency = currencies.find(data => data.id_currency === id_currency);
    let rate = getCurrencyRate(currency.currency_rate, date);

    this.setState({ currency_is_base: currency.is_base });

    if (Array.isArray(opening_balances)) {
      this.setState({
        currency_rate: rate,
        opening_balances: opening_balances
      });
    } else {
      // set ke state sebagai rate global
      this.setState({ currency_rate: rate });
    }
  };

  handleChangeCurrency = value => {
    // const { getFieldValue } = this.props.form;
    // this.setFieldRate(value, getFieldValue("opening_balance_date"));
    this.setFieldRate(value, moment());
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { newAccount, account } = this.props;
        const { opening_balances } = this.state;
        const data = newAccount
          ? newAccount
          : account.account
            ? account.account
            : [];
        const prefix = newAccount
          ? data.account_code
          : data.parent
            ? data.parent.account_code + "."
            : "";

        newAccount
          ? newAccount.account_code
            ? (values.account_code =
              newAccount.account_code + values.account_code)
            : (values.account_code = values.account_code)
          : (values.account_code = prefix + values.account_code);

        const modified = {
          account_code: values.account_code,
          account_name: values.account_name,
          description: values.description,
          id_account_type: values.id_account_type,
          id_currency: values.id_currency,
          id_cash_flow_type_deposit: values.id_cash_flow_type_deposit,
          id_cash_flow_type_withdrawal: values.id_cash_flow_type_withdrawal,
          opening_balances: opening_balances
        };

        // console.log(modified);
        this.props.submit(modified);
      }
    });
  };

  handleModal = () => {
    this.setState({ visible: !this.state.visible });
  };

  handleSubmitModal = values => {
    this.setState({ opening_balances: values });
    this.handleModal();
  };

  render() {
    // console.log(this.props);
    const { currency_rate, visible } = this.state;
    const { getFieldDecorator, getFieldValue, getFieldError } = this.props.form;
    const { account, newAccount, activate, deactivate } = this.props;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const { loadingAccount, loadingSubmit } = account;
    let data = newAccount
      ? Object.assign({}, newAccount)
      : account.account
        ? Object.assign({}, account.account)
        : [];
    const prefix = newAccount
      ? newAccount.account_code
      : data.parent
        ? data.parent.account_code + "."
        : null;

    data.account_code && prefix
      ? (data.account_code = data.account_code.substr(prefix.length))
      : (data.account_code = data.account_code);

    const used = data.is_used;
    const error = account.errorField ? account.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const disabledOpeningBalance = () => {
      if (newAccount) {
        return false;
      } else if (used && Array.isArray(data.opening_balances) && data.opening_balances.length === 0) {
        return true;
      } else if (data.has_child || !data.is_active) {
        return true;
      } else {
        return false;
      }
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="accounts.add.subtitle.data" />
        </Divider> */}
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="accounts.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : null}

        <ModalOpeningBalance
          submit={this.handleSubmitModal}
          visible={visible}
          handleModal={this.handleModal}
          opening_balances={newAccount ? [] : data.opening_balances}
          used={used ? true : false}
          id_currency={getFieldValue("id_currency")}
          rate={currency_rate}
          currencies={account.currencies}
          locations={account.locations}
          loadingForeign={account.loadingForeign}
        />

        <Row>
          <Col span={24}>
            <InputGroup compact>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.type" />}
                >
                  {getFieldDecorator("id_account_type", {
                    initialValue: data.id_account_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages key={0} id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={account.loadingForeign}
                      showSearch
                      disabled={
                        used
                          ? true
                          : newAccount && newAccount.id_account_parent
                            ? true
                            : false
                      }
                      style={{ width: "100%" }}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {account.accountTypes.map((data, key) => (
                        <Option key={key} value={data.id_account_type}>
                          {data.account_type_name}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.currency" />}
                >
                  {getFieldDecorator("id_currency", {
                    initialValue: data.id_currency,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages key={0} id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={account.loadingForeign}
                      showSearch
                      disabled={used ? true : false}
                      style={{ width: "100%" }}
                      onChange={this.handleChangeCurrency}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {account.currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code + " (" + data.currency_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup compact>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.code" />}
                  validateStatus={
                    error.account_code || getFieldError("account_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.account_code
                      ? error.account_code
                      : getFieldError("account_code")
                  }
                >
                  {getFieldDecorator("account_code", {
                    initialValue: newAccount ? null : data.account_code,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages key={0} id="required" />
                      },
                      {
                        max: 30,
                        message: <IntlMessages key={1} id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      addonBefore={prefix}
                      disabled={used ? true : false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.name" />}
                  validateStatus={
                    error.account_name || getFieldError("account_name")
                      ? "error"
                      : null
                  }
                  help={
                    error.account_name
                      ? error.account_name
                      : getFieldError("account_name")
                  }
                >
                  {getFieldDecorator("account_name", {
                    initialValue: data.account_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages key={0} id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages key={1} id="maxLength50" />
                      }
                    ]
                  })(<Input disabled={used ? true : false} />)}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup compact>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.cashFlowTypeDeposit" />}
                >
                  {getFieldDecorator("id_cash_flow_type_deposit", {
                    initialValue: data.id_cash_flow_type_deposit,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      allowClear
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="accounts.cashFlowTypeDeposit" />
                      }
                      loading={account.loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                      disabled={used ? true : false}
                    >
                      {account.cashFlowTypes.map((data, key) =>
                        data.transaction_type === 1 ? (
                          <Option key={key} value={data.id_cash_flow_type}>
                            {`${data.cash_flow_type_code} (${
                              data.description
                              })`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.cashFlowTypeWithdrawal" />}
                >
                  {getFieldDecorator("id_cash_flow_type_withdrawal", {
                    initialValue: data.id_cash_flow_type_withdrawal,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      allowClear
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="accounts.cashFlowTypeWithdrawal" />
                      }
                      loading={account.loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                      disabled={used ? true : false}
                    >
                      {account.cashFlowTypes.map((data, key) =>
                        data.transaction_type === 2 ? (
                          <Option key={key} value={data.id_cash_flow_type}>
                            {`${data.cash_flow_type_code} (${
                              data.description
                              })`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="accounts.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description
              })(<TextArea autosize={{ minRows: 2 }} />)}
            </Form.Item>
          </Col>
        </Row>

        <Row style={{ marginBottom: "24px" }}>
          <Col span={24} style={{ display: "flex" }}>
            <Button
              type="dashed"
              onClick={this.handleModal}
              style={{ width: "50%", margin: "0 auto" }}
              disabled={disabledOpeningBalance()}
            >
              <Icon type="plus" />{" "}
              <IntlMessages id="button.openingBalanceLocation" />
            </Button>
          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <InputGroup compact>
              {newAccount && permissions.includes("Create Account") ? (
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginRight: "15px" }}
                      loading={loadingSubmit || loadingAccount ? true : false}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              ) : null}

              {!newAccount && permissions.includes("Update Account") ? (
                <Col>
                  <Form.Item {...tailFormItemLayout}>
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ marginRight: "15px" }}
                      loading={loadingSubmit || loadingAccount ? true : false}
                    >
                      <IntlMessages id="button.submit" />
                    </Button>
                  </Form.Item>
                </Col>
              ) : null}

              {newAccount ? null : permissions.includes("Update Account") ? (
                <Col>
                  {data.is_active ? (
                    <Form.Item {...tailFormItemLayout}>
                      <Popconfirms
                        title={
                          <IntlMessages id="accounts.popconfirm.deactivate" />
                        }
                        onConfirm={e => {
                          e.preventDefault();
                          deactivate(data.id_account);
                        }}
                        okText={getLanguage({ id: "yes" })}
                        cancelText={getLanguage({ id: "no" })}
                      >
                        <Button
                          type="danger"
                          loading={
                            loadingSubmit || loadingAccount ? true : false
                          }
                          disabled={
                            used ||
                              (Array.isArray(data.opening_balances) && data.opening_balances.length > 0)
                              ? true
                              : false
                          }
                        >
                          <IntlMessages id="tooltip.deactivate" />
                        </Button>
                      </Popconfirms>
                    </Form.Item>
                  ) : (
                      <Form.Item {...tailFormItemLayout}>
                        <Popconfirms
                          title={
                            <IntlMessages id="accounts.popconfirm.activate" />
                          }
                          onConfirm={e => {
                            e.preventDefault();
                            activate(data.id_account);
                          }}
                          okText={getLanguage({ id: "yes" })}
                          cancelText={getLanguage({ id: "no" })}
                        >
                          <Button
                            type="default"
                            style={{ backgroundColor: "#00B16A", color: "#FFFFFF", }}
                            loading={
                              loadingSubmit || loadingAccount ? true : false
                            }
                            disabled={used ? true : false}
                          >
                            <IntlMessages id="tooltip.activate" />
                          </Button>
                        </Popconfirms>
                      </Form.Item>
                    )}
                </Col>
              ) : null}
            </InputGroup>
          </Col>
        </Row>
      </Form>
    );
  }
}

const WrappedAccountForm = Form.create({ name: "account" })(AccountForm);

export default WrappedAccountForm;
